:root {
  --background: #253842;
  --background: linear-gradient(206.15deg, #06182E 12.92%, #000000 83.54%);


  --green: #219653;
  --blue: #2F80ED;
  --purple: #9B51E0;
  --red: #EB5757;
  --orange: #F2994A;
  --mauve: #BB6BD9;
  --yellow: #F2C94C;
}





@import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');

html, body {
  background: var(--background);
/*   background: -webkit-gradient(linear, 100% 0%, 100% 0%, from(black), to(white)); */
/*   background: -webkit-gradient(linear, 0% 0% 0% 100%, from(rgba(6, 24, 46)), to(rgba(0,0,0,0))); */
  color: white;
  overflow: hidden;
  min-height: 100vh;
  min-width: 100vw;

  animation: bg-fade 5s;
  animation-delay: 5s;
}

h1, h2, h3, h4 {
  font-family: 'Gloock', serif;
  color: white;
  margin-top: 0;
}

@keyframes bg-fade {
  from {
    background: black;
  }
  to {
    background: var(--background);
  }
}

#tag {
  font-size: 7vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Individual Blocks */

.block {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  border-radius: 1vmax;
  height: 15vh;
  width: 20vw;

  padding: 1%;

  background: var(--red);
  transition: transform 500ms ease;

  opacity: 0;
  animation: 1000s ease-in appear;

}

@keyframes appear {
  0% {
    opacity: 0;

  }
  0.1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.block:hover {
  transform: scale(1.1);
}

.block:nth-child(1) {
  background: var(--green);
  top: 10vh;
  left: 15vw;
  animation-delay: 1s;
}

.block:nth-child(2) {
  background: var(--blue);
  top: 20vh;
  left: 50vw;
  animation-delay: 1.5s;
}


.block:nth-child(3) {
  background: var(--purple);
  top: 50vh;
  left: 5vw;
  animation-delay: 2s;
}

.block:nth-child(4) {
  background: var(--red);
  top: 75vh;
  left: 20vw;
  animation-delay: 2.5s;
}

.block:nth-child(5) {
  background: var(--orange);
  top: 50vh;
  left: 70vw;
  animation-delay: 3s;
}
