:root {
	--carousel-width: 85vw;
	--card-height: 80vh;
	--gap-left: 3vw;
	--gap-right: 3vw;
}

h2 {
	font-size: 2vw;
}

.carousel-container {
	padding: 4vh 4vw;
}

.carousel-inner {
	display: flex;
	flex-wrap: nowrap;

	max-width: var(--carousel-width);
/* 	Card Slide Animation */
	transition: transform 0.5s;
}


#header {
	grid-area: header;
	font-size: 6vw;
}

#main-img {
	grid-area: main-img;
	width: 80%;
}

.overview-container {
	grid-area: overview;
}

.tools-container {
	grid-area: tools;
}

.links-container {
	grid-area: links;
}

.citem {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;

	min-width: 100%;
	margin-left: var(--gap-left);
	margin-right: var(--gap-right);

	display: grid;
	grid-template-columns: [col1] 45% [col2] auto;
	grid-template-rows: [row1] 20% [row2] auto [row3] auto [row4] 8%;
	column-gap: var(--gaps);
	grid-template-areas:
	"header header"
	"main-img overview"
	"main-img tools"
	"main-img links";	
}

.citem {

	border-radius: 1vw;
	height: var(--card-height);
	padding: 2vw;

}

.citem:nth-child(1) {
	background: var(--green);
}

.citem:nth-child(2) {
	background: var(--blue);
}

.citem:nth-child(3) {
	background: var(--purple);
}

.citem:nth-child(4) {
	background: var(--red);
}

.citem:nth-child(5) {
	background: var(--orange);
}



/* Bottom Bar */

.bar {
	height: 7vh;
	width: var(--carousel-width);
/* 	width: 60vw; */
	margin-left: 7vw;

	background: linear-gradient(123.57deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.1) 104.93%);
	border: 2px solid rgba(255, 255, 255, 0.5);
	box-shadow: 0px 4px 24px -1px rgba(0, 0, 0, 0.25);
	backdrop-filter: blur(5px);
	border-radius: 50px;

	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;

/* 	transform: matrix(1, 0, 0, -1, 0, 0); */
}

.bar button {
	font-weight: 900;
	font-size: 1.5vw;
	display: flex;
	justify-content: center;
	align-items: center;
	background: none;
	border: none;
	color: white;
	cursor: pointer;
}
